// app/upload/page.tsx

'use client';

import { useState } from 'react';
import { FileUpload } from '@/components/ui/FileUpload';
import { Button } from '@/components/ui/Button';
import { Alert, AlertDescription } from '@/components/ui/Alert';
import {
    Upload,
    FileText,
    CheckCircle,
    CheckCircle2,
    AlertCircle,
    ArrowLeft,
    Image,
    File,
} from 'lucide-react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';

export default function UploadPage() {
    const router = useRouter();
    const [uploadResults, setUploadResults] = useState<any[]>([]);
    const [showResults, setShowResults] = useState(false);

    const handleUploadComplete = (results: any[]) => {
        console.log('Upload completed:', results);
        setUploadResults(results);
        setShowResults(true);
    };

    const resetUpload = () => {
        setUploadResults([]);
        setShowResults(false);
    };

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <div className="bg-white shadow-sm border-b">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex items-center justify-between h-16">
                        <div className="flex items-center space-x-4">
                            <Link href="/">
                                <Button variant="ghost" size="sm">
                                    <ArrowLeft className="w-4 h-4 mr-2" />
                                    Back to Home
                                </Button>
                            </Link>

                            <div className="h-6 w-px bg-gray-300" />

                            <div className="flex items-center space-x-2">
                                <Upload className="w-5 h-5 text-blue-600" />
                                <h1 className="text-xl font-semibold text-gray-900">
                                    File Upload
                                </h1>
                            </div>
                        </div>

                        {showResults && (
                            <Button
                                variant="outline"
                                onClick={resetUpload}
                            >
                                Upload More Files
                            </Button>
                        )}
                    </div>
                </div>
            </div>

            {/* Main Content */}
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                {!showResults ? (
                    // Upload Interface
                    <div className="space-y-6">
                        {/* Instructions */}
                        <div className="bg-white rounded-lg shadow-sm border p-6">
                            <div className="flex items-start space-x-3">
                                <FileText className="w-6 h-6 text-blue-600 mt-1" />
                                <div>
                                    <h2 className="text-lg font-medium text-gray-900 mb-2">
                                        Upload Your Study Materials
                                    </h2>
                                    <p className="text-gray-600 mb-4">
                                        Upload documents, images, and other study materials to organize your learning resources.
                                        All files are securely stored and easily accessible.
                                    </p>

                                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>PDF Documents</span>
                                        </div>
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>Images (JPG, PNG, GIF)</span>
                                        </div>
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>Word Documents</span>
                                        </div>
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>Text Files</span>
                                        </div>
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>Max 10MB per file</span>
                                        </div>
                                        <div className="flex items-center space-x-2">
                                            <CheckCircle className="w-4 h-4 text-green-500" />
                                            <span>Up to 10 files</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Upload Component */}
                        {/* <div className="bg-white rounded-lg shadow-sm border p-6">
                            <FileUpload
                                onUploadComplete={handleUploadComplete}
                                maxFiles={10}
                            />
                        </div> */}
                    </div>
                ) : (
                    // Results Display
                    <div className="space-y-6">
                        <div className="bg-white rounded-lg shadow-sm border p-6">
                            <div className="flex items-center space-x-3 mb-4">
                                <CheckCircle className="w-8 h-8 text-green-500" />
                                <div>
                                    <h2 className="text-xl font-semibold text-gray-900">
                                        Upload Successful!////
                                    </h2>
                                    <p className="text-gray-600">
                                        {uploadResults.length} file{uploadResults.length > 1 ? 's' : ''} uploaded successfully
                                    </p>
                                </div>
                            </div>

                            {/* Results List */}
                            <div className="space-y-3">
                                {uploadResults.map((result, index) => (
                                    <div key={index} className="flex items-center justify-between p-3 bg-green-50 rounded-lg border border-green-200">
                                        <div className="flex items-center space-x-3">
                                            <CheckCircle className="w-5 h-5 text-green-600" />
                                            <div>
                                                <p className="font-medium text-green-900">
                                                    {result?.data?.attachment?.originalName || 'Unknown file'}
                                                </p>
                                                <p className="text-sm text-green-700">
                                                    {result?.data?.attachment?.size ?
                                                        `${(result.data.attachment.size / 1024).toFixed(1)} KB` :
                                                        'Size unknown'
                                                    } •
                                                    {result?.data?.attachment?.mimeType || 'Unknown type'}
                                                </p>
                                            </div>
                                        </div>

                                        {result?.data?.attachment?.url && (
                                            <Button
                                                variant="outline"
                                                size="sm"
                                                onClick={() => window.open(result.data.attachment.url, '_blank')}
                                            >
                                                View File
                                            </Button>
                                        )}
                                    </div>
                                ))}
                            </div>

                            {/* Actions */}
                            <div className="flex justify-center space-x-4 mt-6 pt-6 border-t">
                                <Button
                                    variant="outline"
                                    onClick={resetUpload}
                                >
                                    Upload More Files
                                </Button>

                                <Link href="/dashboard">
                                    <Button>
                                        Go to Dashboard
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    </div>
                )}
            </div>


            {/* Upload Results Summary */}
            {uploadResults.length > 0 && (
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pb-8">
                    <div className="bg-gradient-to-r from-green-50 to-blue-50 border border-green-200 rounded-xl p-6 shadow-sm">
                        {/* Header */}
                        <div className="flex items-center justify-between mb-6">
                            <div className="flex items-center space-x-3">
                                <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center">
                                    <CheckCircle className="w-8 h-8 text-green-500" />
                                </div>
                                <div>
                                    <h3 className="text-xl font-bold text-gray-900">
                                        🎉 Upload Complete!
                                    </h3>
                                    <p className="text-gray-600">
                                        {uploadResults.length} study material{uploadResults.length > 1 ? 's' : ''} successfully processed
                                    </p>
                                </div>
                            </div>

                            {/* Quick Actions */}
                            <div className="flex space-x-2">
                                <Button
                                    variant="outline"
                                    size="sm"
                                    onClick={() => window.location.reload()}
                                >
                                    📁 Add More
                                </Button>
                                <Button
                                    size="sm"
                                    onClick={() => router.push('/dashboard')}
                                    className="bg-blue-600 hover:bg-blue-700"
                                >
                                    🧠 Start Studying
                                </Button>
                            </div>
                        </div>

                        {/* Upload Summary Grid */}
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                            {uploadResults.map((result, index) => (
                                <div
                                    key={index}
                                    className="bg-white border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
                                >
                                    {/* File Icon & Status */}
                                    <div className="flex items-start justify-between mb-3">
                                        <div className="flex items-center space-x-2">
                                            <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
                                                {result?.data?.attachment?.originalName?.toLowerCase().endsWith('.pdf') ? (
                                                    <FileText className="w-4 h-4 text-red-600" />
                                                ) : result?.data?.attachment?.originalName?.toLowerCase().endsWith('.docx') ? (
                                                    <FileText className="w-4 h-4 text-blue-600" />
                                                ) : result?.data?.attachment?.originalName?.toLowerCase().match(/\.(jpg|jpeg|png|gif)$/) ? (
                                                    <Image className="w-4 h-4 text-green-600" />
                                                ) : (
                                                    <File className="w-4 h-4 text-gray-600" />
                                                )}
                                            </div>
                                            <span className="text-xs font-medium text-green-600 bg-green-100 px-2 py-1 rounded-full">
                                                ✅ Ready
                                            </span>
                                        </div>
                                    </div>

                                    {/* File Details */}
                                    <div className="space-y-2">
                                        <h4 className="font-medium text-gray-900 text-sm truncate" title={result?.data?.attachment?.originalName}>
                                            {result?.data?.attachment?.originalName || 'Unknown file'}
                                        </h4>

                                        <div className="flex items-center justify-between text-xs text-gray-500">
                                            <span>
                                                {result?.data?.attachment?.size ?
                                                    `${(result.data.attachment.size / 1024).toFixed(1)} KB` :
                                                    'Size unknown'
                                                }
                                            </span>
                                            <span className="truncate ml-2">
                                                {result?.data?.attachment?.mimeType?.split('/')[1]?.toUpperCase() || 'FILE'}
                                            </span>
                                        </div>

                                        {/* File Actions */}
                                        <div className="flex space-x-2 pt-2">
                                            <Button
                                                variant="outline"
                                                size="sm"
                                                className="flex-1 text-xs"
                                                onClick={() => {
                                                    if (result?.data?.attachment?.url) {
                                                        window.open(result.data.attachment.url, '_blank');
                                                    }
                                                }}
                                                disabled={!result?.data?.attachment?.url}
                                            >
                                                👁️ View
                                            </Button>
                                            <Button
                                                variant="ghost"
                                                size="sm"
                                                className="flex-1 text-xs"
                                                onClick={() => {
                                                    // Add to study session logic
                                                    console.log('Add to study:', result);
                                                }}
                                            >
                                                📚 Study
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>

                        {/* Upload Statistics */}
                        <div className="mt-6 pt-4 border-t border-gray-200">
                            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
                                <div>
                                    <p className="text-2xl font-bold text-gray-900">
                                        {uploadResults.length}
                                    </p>
                                    <p className="text-sm text-gray-500">Documents</p>
                                </div>
                                <div>
                                    <p className="text-2xl font-bold text-gray-900">
                                        {(() => {
                                            const totalSize = uploadResults.reduce((total, result) =>
                                                total + (result?.data?.attachment?.size || 0), 0
                                            );
                                            return totalSize > 1024 * 1024 ?
                                                `${(totalSize / (1024 * 1024)).toFixed(1)}MB` :
                                                `${(totalSize / 1024).toFixed(1)}KB`;
                                        })()}
                                    </p>
                                    <p className="text-sm text-gray-500">Total Size</p>
                                </div>
                                <div>
                                    <p className="text-2xl font-bold text-green-600">
                                        {uploadResults.filter(r => r?.success).length}
                                    </p>
                                    <p className="text-sm text-gray-500">Successful</p>
                                </div>
                                <div>
                                    <p className="text-2xl font-bold text-blue-600">Ready</p>
                                    <p className="text-sm text-gray-500">Status</p>
                                </div>
                            </div>
                        </div>

                        {/* Next Steps */}
                        <div className="mt-6 pt-4 border-t border-gray-200">
                            <h4 className="font-medium text-gray-900 mb-3">🚀 What's Next?</h4>
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                                <div className="flex items-center space-x-3 p-3 bg-blue-50 rounded-lg border border-blue-200">
                                    <span className="text-2xl">🧠</span>
                                    <div>
                                        <p className="font-medium text-blue-900">Start Learning</p>
                                        <p className="text-sm text-blue-700">Generate quizzes and flashcards</p>
                                    </div>
                                </div>
                                <div className="flex items-center space-x-3 p-3 bg-purple-50 rounded-lg border border-purple-200">
                                    <span className="text-2xl">📊</span>
                                    <div>
                                        <p className="font-medium text-purple-900">Track Progress</p>
                                        <p className="text-sm text-purple-700">Monitor your study sessions</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}